.TransactionPair__circle {
  border-radius: 50%;
  display: block;
  height: 7px;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 7px;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%; }

.TransactionPair__circle--one {
  -webkit-animation: circle-one-pulse linear 1.5s infinite;
  animation: circle-one-pulse linear 1.5s infinite;
  background: #1559ff;
  -webkit-box-shadow: 0 0 2px 2px rgba(21, 89, 255, 0.39);
  box-shadow: 0 0 2px 2px rgba(21, 89, 255, 0.39); }

.TransactionPair__circle--two {
  -webkit-animation: circle-two-pulse linear 1.5s infinite;
  animation: circle-two-pulse linear 1.5s infinite;
  background: #00f1b7;
  -webkit-box-shadow: 0 0 2px 2px rgba(0, 241, 183, 0.39);
  box-shadow: 0 0 2px 2px rgba(0, 241, 183, 0.39); }

@-webkit-keyframes circle-one-pulse {
  0% {
    -webkit-box-shadow: 0 0 2px 2px rgba(21, 89, 255, 0.39);
    box-shadow: 0 0 2px 2px rgba(21, 89, 255, 0.39); }
  50% {
    -webkit-box-shadow: 0 0 0px 0px rgba(21, 89, 255, 0.39);
    box-shadow: 0 0 0px 0px rgba(21, 89, 255, 0.39); }
  100% {
    -webkit-box-shadow: 0 0 2px 2px rgba(21, 89, 255, 0.39);
    box-shadow: 0 0 2px 2px rgba(21, 89, 255, 0.39); } }

@keyframes circle-one-pulse {
  0% {
    -webkit-box-shadow: 0 0 2px 2px rgba(21, 89, 255, 0.39);
    box-shadow: 0 0 2px 2px rgba(21, 89, 255, 0.39); }
  50% {
    -webkit-box-shadow: 0 0 0px 0px rgba(21, 89, 255, 0.39);
    box-shadow: 0 0 0px 0px rgba(21, 89, 255, 0.39); }
  100% {
    -webkit-box-shadow: 0 0 2px 2px rgba(21, 89, 255, 0.39);
    box-shadow: 0 0 2px 2px rgba(21, 89, 255, 0.39); } }

@-webkit-keyframes circle-two-pulse {
  0% {
    -webkit-box-shadow: 0 0 2px 2px rgba(0, 241, 183, 0.39);
    box-shadow: 0 0 2px 2px rgba(0, 241, 183, 0.39); }
  50% {
    -webkit-box-shadow: 0 0 0px 0px rgba(0, 241, 183, 0.39);
    box-shadow: 0 0 0px 0px rgba(0, 241, 183, 0.39); }
  100% {
    -webkit-box-shadow: 0 0 2px 2px rgba(0, 241, 183, 0.39);
    box-shadow: 0 0 2px 2px rgba(0, 241, 183, 0.39); } }

@keyframes circle-two-pulse {
  0% {
    -webkit-box-shadow: 0 0 2px 2px rgba(0, 241, 183, 0.39);
    box-shadow: 0 0 2px 2px rgba(0, 241, 183, 0.39); }
  50% {
    -webkit-box-shadow: 0 0 0px 0px rgba(0, 241, 183, 0.39);
    box-shadow: 0 0 0px 0px rgba(0, 241, 183, 0.39); }
  100% {
    -webkit-box-shadow: 0 0 2px 2px rgba(0, 241, 183, 0.39);
    box-shadow: 0 0 2px 2px rgba(0, 241, 183, 0.39); } }

@media screen and (min-width: 1145px) {
  .TransactionPair__circle {
    height: 8px;
    width: 8px; } }



